<template>
  <div class="song-list">
    <MusicLoading :loading="loading"></MusicLoading>
    <MusicHeader></MusicHeader>
    <div class="container">
      <div class="titles">
        <h2><span class="iconfont"></span>&nbsp;语种</h2>
        <h2><span class="iconfont"></span>&nbsp;流派</h2>
        <h2><span class="iconfont"></span>&nbsp;情感</h2>
        <h2><span class="iconfont"></span>&nbsp;场景</h2>
        <h2><span class="iconfont"></span>&nbsp;主题</h2>
      </div>
      <div class="categories">
        <div class="category" v-for="(c, i) in categories" :key="i">
          <ul class="clearfix">
            <template v-if="c.categoryName == '语种'">
              <li
                v-for="(sub, j) in c.subCate.filter((item, i) => i < 5)"
                :key="j"
              >
                <router-link :to="'/songlist/' + sub.id" class="item ellipsis">
                  {{ sub.categoryName }}
                </router-link>
              </li>
            </template>
            <template v-else>
              <li
                v-for="(sub, j) in c.subCate.filter((item, i) => i < 8)"
                :key="j"
              >
                <router-link :to="'/songlist/' + sub.id" class="item ellipsis">
                  {{ sub.categoryName }}
                </router-link>
              </li>
            </template>
            <li>
              <el-popover placement="bottom" width="350" trigger="click">
                <a slot="reference" href="javascript:void(0)" class="item more">
                  更多<i></i>
                </a>
                <div class="more-content">
                  <ul class="clearfix">
                    <template v-if="c.categoryName == '语种'">
                      <li
                        v-for="(sub, j) in c.subCate.filter(
                          (item, i) => i >= 5
                        )"
                        :key="j"
                      >
                        <router-link
                          :to="'/songlist/' + sub.id"
                          class="item ellipsis"
                        >
                          {{ sub.categoryName }}
                        </router-link>
                      </li>
                    </template>
                    <template v-else>
                      <li
                        v-for="(sub, j) in c.subCate.filter(
                          (item, i) => i >= 8
                        )"
                        :key="j"
                      >
                        <router-link
                          :to="'/songlist/' + sub.id"
                          class="item ellipsis"
                        >
                          {{ sub.categoryName }}
                        </router-link>
                      </li>
                    </template>
                  </ul>
                </div>
              </el-popover>
            </li>
          </ul>
        </div>
      </div>
      <div class="songs">
        <div class="list-head">
          {{ selectedCagegoryName }}
          <span
            class="iconfont"
            v-if="selectedCagegoryName != '全部歌单'"
            @click="$router.push({ name: 'SongList' })"
            ></span
          >
        </div>
        <div class="list">
          <div class="song" v-for="(s, i) in songs" :key="i">
            <router-link :to="'/songdetail/' + s.id">
              <div class="logo">
                <img :src="s.pic" :alt="s.title" />
                <div class="play-bar">
                  <span class="iconfont"></span>
                </div>
              </div>
              <div class="title">
                {{ s.title }}
              </div>
            </router-link>
          </div>
        </div>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="pageNo"
          @current-change="changePage"
        >
        </el-pagination>
      </div>
      <MusicLive />
    </div>
    <MusicFooter></MusicFooter>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "SongList",
  data() {
    return {
      categories: [],
      selectedCagegoryName: "全部歌单",
      pageSize: 50,
      songs: [],
      loading: false,
      total: 0,
      pageSize: 50,
      pageNo: this.$route.query.pageNo - 0 || 1,
    };
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.getCategories();
      vm.getSongs(vm.pageNo, vm.$route.params.subCateId);
    });
  },
  methods: {
    changePage(pageNo) {
      this.pageNo = pageNo;
      var subCateId = this.$route.params.subCateId - 0 || "";
      this.$router.push({
        name: "SongList",
        params: { subCateId },
        query: { pageNo },
      });
    },
    getCategories() {
      this.loading = true;
      axios
        .get("/api/v1/tracklist/category")
        .then((res) => {
          this.categories = res.data.data;
          this.loading = false;
        })
        .catch((err) => {
          this.loading = false;
        });
    },
    getSongs(pageNo, subCateId) {
      if (!pageNo) pageNo = 1;
      if (!subCateId) subCateId = "";
      var url = `/api/v1/tracklist/list?pageNo=${pageNo}&pageSize=${this.pageSize}&subCateId=${subCateId}`;
      this.loading = true;
      axios
        .get(url)
        .then((res) => {
          this.songs = res.data.data.result;
          this.total = res.data.data.total;
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
  beforeRouteUpdate(to, from, next) {
    this.getCategories();
    this.pageNo = to.query.pageNo - 0 || 1;
    this.getSongs(this.pageNo, to.params.subCateId);
    if (to.params.subCateId) {
      this.categories.forEach((c) => {
        c.subCate.forEach((sub) => {
          if (sub.id == to.params.subCateId) {
            this.selectedCagegoryName = sub.categoryName;
            return;
          }
        });
      });
    } else{
      this.selectedCagegoryName = '全部歌单';
    }

    next();
  },
};
</script>

